<script>
    import { onDestroy } from 'svelte';
    import { data } from '../store'

    var chapters = [];
    var currentSurah = null;
    

    var showChapters = true;
    var showChaptersCB = () => {
        if(window.outerWidth > 991) {
            showChapters = true;
        } else {
            if(currentSurah !== null) {
                showChapters = false;
            } else {
                showChapters = true;
            }
        }
    }

    
    var unsubscribe = data.subscribe(object => {
        chapters = object.chapters
        if(object.currentSurah === null) {
            return;
        }
        currentSurah = object.currentSurah
        showChaptersCB();
        
    })

    showChaptersCB();

    var selectChapter = id => {
        data.update(object => {
            object.currentSurah = id;
            showChapters = true
            return object;
        })
    }

    onDestroy(() => {
        unsubscribe();
    })
    
    

</script>
{#if showChapters }
<div class="chapters-list">

    <div class="chapters__filter">
        <button type="button" class="btn">Sort by</button>
    </div>

    {#each chapters as chapter, i }
    <div class="chapter{currentSurah === chapter.id ? ' active': ''}" data-id="{chapter.id}" on:click="{selectChapter(chapter.id)}">
        <span class="chapter__no">{chapter.chapter_number}</span>
        <div class="chapter__info">
            <span class="chapter__name">{chapter.name_simple}</span>
            <span class="chapter__name-translate">{chapter.translated_name.name}</span>
            <span class="chapter__verses badge">{chapter.verses_count} Verses</span>
        </div>
    </div>
    {/each}
    
</div>
{/if}