<script>
	import { onMount, onDestroy } from 'svelte';
	import { data } from './components/store.js'
	import axios from 'axios'
	import Header from "./components/Header/Header.svelte";
	import Sidebar from './components/Main/Sidebar.svelte'
	import Surah from './components/Main/Surah.svelte'

	var chapters = [];

	onMount(() => {
		if(chapters.length === 0) {
			axios.get( 'https://api.quran.com/api/v3/chapters' ).then(function(resp){
				data.update(object => {
					object.chapters = resp.data.chapters
					return object;
				})
			})
			.catch(function(error){
				console.log(error);
			});
		}
	})


	var unsub = data.subscribe((object) => {
		chapters = object.chapters;
	})
	
	onDestroy(() => {
		unsub();
	})
	

</script>

<div id="app">
	<Header/>

	<main class="main-content">
		<div class="content__inner">
			<Sidebar/>
			<Surah/>
		</div>
	</main>

</div>